import React from 'react';
import './Research.css';

class Paper extends React.Component{
    
    render() {
	return (
	    <div class="row">
		<div class="col">
		    <p class="Paper">
			{this.props.authors} &nbsp; <br/>
			<b>{this.props.title} &nbsp;</b><br/>
			{this.props.conference}  &nbsp; <a href={this.props.pdf} target="_blank" rel="noopener noreferrer">[paper]</a>&nbsp; <a href={this.props.code} target="_blank" rel="noopener noreferrer">[code]</a>&nbsp; 
		    </p>	
		</div>
	    </div>
	)
	
    }

}


export default Paper

